class GetCompanyForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      fields: {
        [clientType]: {
          validators: [],
          value:"ИП"
        },
        [bin_iin]: {
          value: "",
          validators: [isRequired, isBin],
        },
        [companyName]: {
          value: "",
          validators: [],
        },
        [city]: {
          value: [],
          validators: [isRequired]
        },
        [branch]: {
          value: [],
          validators: [isRequired]
        }
      },
      mapStatus: 'clean'
    };
    Object.keys(this.state.fields).map(field => {
      this.state.fields[field].flags = {...defaultFlags};
      return field;
    });
    
    this.handleBlur = this.handleBlur.bind(this);
    this.handleFocus = this.handleFocus.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }
  
  componentDidMount() {
    this.setState({
      mapStatus: 'loading'
    });
  }
  
  render() {
    const {fields} = this.state;
    return (
      <Form className="row pb-0">
        <div className="col-md-6">
          <Heading size="s">Информация о вашей компании</Heading>
          <FormField className="mt-4">
            <Label size="l" className="opacity-high mr-3">Тип клиента</Label>
            <Switch value={fields[clientType].value}
                    onText="ИП"
                    offText="ЮЛ"
                    onColor="rgba(11, 31, 53, 0.9)"
                    offColor="rgba(3, 51, 144, 0.9)"
                    onValue="ИП"
                    offValue="ЮЛ"
                    onChange={(v) => this.handleChange(clientType, v)}
                    onFocus={() => this.handleFocus(clientType)}
                    onBlur={() => this.handleBlur(clientType)}
            />
          </FormField>
          <FormField>
            <Input label={fields[clientType].value==='ЮЛ' ? 'БИН' : 'ИИН'}
                   width="available"
                   className="input-required"
                   mask="### ### ### ###"
                   value={fields[bin_iin].value}
                   onChange={(v) => this.handleChange(bin_iin, v)}
                   onFocus={() => this.handleFocus(bin_iin)}
                   onBlur={() => this.handleBlur(bin_iin)}
                   leftAddons={(fields[bin_iin].flags.focused || fields[bin_iin].value) && <SearchIcon size="s"/>}/>
          </FormField>
          <FormField>
            <Input label={fields[clientType].value==='ЮЛ' ? 'Наименование организации' : 'Наименование ИП'}
                   width="available"
                   value={fields[companyName].value}
                   onChange={(v) => this.handleChange(companyName, v)}
                   onFocus={() => this.handleFocus(companyName)}
                   onBlur={() => this.handleBlur(companyName)}/>
          </FormField>
          <FormField>
            <Select label="Город"
                    width="available"
                    mode="radio"
                    options={cities}
                    value={fields[city].value}
                    onChange={(v) => this.handleChange(city, v)}
                    onFocus={() => this.handleFocus(city)}
                    onBlur={() => this.handleBlur(city)}/>
          </FormField>
        </div>
      </Form>
    )
  }
  
  handleFocus(field) {
    const newState = {...this.state};
    newState.fields[field].flags.focused = true;
    this.setState(newState);
  }
  handleBlur(field) {
    const newState = {...this.state};
    newState.fields[field].flags.focused = false;
    newState.fields[field].flags.touched = true;
    this.setState(newState);
  }
  handleChange(field, val) {
    const newState = {...this.state};
    newState.fields[field].flags.dirty = true;
    newState.fields[field].value  = val;
    this.setState(newState);
  }
  
}

export default GetCompanyForm;
